.messages-wrapper {
  width: 100%;
  height: 100vh;

  .bottom-contents {
    display: flex;
    width: 100%;
    height: calc(100% - 120px);

    .left-side {
      padding-top: 20px;
      width: 100%;
      height: 100%;
      padding-left: 80px;
      padding-right: 80px;

      .title {
        text-align: center;
        font-weight: bold;
        font-size: 28px;
        color: rgba(133, 145, 158, 1);
      }

      .messages {
        margin-top: 30px;
        display: flex;
        align-items: center;
        flex-wrap: wrap;

        .message-box {
          margin-right: 20px;
          margin-top: 20px;
          padding: 15px;
          width: 300px;
          height: 220px;
          border-radius: 10px;
          box-shadow: rgba(99, 99, 99, 0.2) 0px 2px 8px 0px;
          position: relative;

          .icon-message {
            position: absolute;
            right: 15px;
            bottom: 10px;
            width: 100px;
            height: 100px;
            border-radius: 50%;
            display: flex;
            align-items: center;
            justify-content: center;

            img {
              width: 70%;
              opacity: 0.2;
            }
          }

          .del-icon {
            position: absolute;
            right: 0px;
            top: 5px;
            width: 45px;
            height: 45px;
            border-radius: 50%;
            display: flex;
            align-items: center;
            justify-content: center;

            img {
              width: 55%;
              cursor: pointer;
              transition: 0.3s;

              &:hover {
                transform: scale(1.1);
              }
            }
          }

          .name {
            font-weight: bold;
            text-align: center;
          }

          .number {
            text-align: center;
            margin-top: 6px;
          }

          .des {
            margin-top: 10px;
            padding: 6px;
            width: 100%;
            height: 110px;
            overflow-y: auto;

            &::-webkit-scrollbar {
              width: 4px;
              height: 6px;
              border-radius: 20px;
            }

            &::-webkit-scrollbar-track {
              box-shadow: inset 0 0 6px rgba(150, 141, 141, 0.3);
            }

            &::-webkit-scrollbar-thumb {
              border-radius: 20px;
              background-color: gray;
            }
          }

          .time{
            width: 100%;
            text-align: right;
            margin-top: 10px;
            font-size: 14px;
            color: #00ADAD;
            font-weight: bold;
            span{
              margin-right: 10px;
              color: black;
            }
          }
        }
      }


    }
  }

  .mobile-navbar-container {
    display: none;
  }
}

@media (max-width: 768px) {
  .messages-wrapper {
    width: 100%;
    height: 100vh;

    .bottom-contents {
      display: flex;
      width: 100%;
      height: calc(100% - 120px);

      .left-side {
        padding-left: 15px;
        padding-right: 15px;

        .title {
          font-size: 22px;
        }
        .messages {
          margin-top: 20px;

          .message-box {
            margin: 10px auto;
            padding: 15px;
            width: 90%;
            height: 200px;
          }
        }
      }
    }

    .mobile-navbar-container {
      width: 100%;
      display: flex;
    }
  }
}